.contact-details-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #130f2a;
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    padding: 1.5rem;
    margin-bottom: 1rem;
}

.contact-details-card icon {
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
    background-color: #3d3072;
    margin-bottom: 0.8rem;
}

.icon img {
    width: 1.8rem;
    object-fit: contain;
}

.contact-details-card a {
    color: white;
    font-size: 0.9rem;
    font-weight: 400;
    text-decoration: none;
    word-break: break-all;
}
.contact-details-card a:hover {
    text-decoration: underline;
}
.contact-details-card a:visited {
    color: white;
}