.skills-info-card {
    min-height: 23rem;
    border-radius: 0.65rem;
    border: 1.5px solid #6751b9;
    background-color: rgba(22, 17, 47, 0.398);
    backdrop-filter: blur(1rem);
}

.skills-info-card h6 {
    font-size: 1.2rem;
    font-weight: 500;
    padding: 0.8rem 2rem;
    background: linear-gradient(180deg, #aa94fe 0%, #7c5fe6 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-bottom: 1.5px solid #6751b9;
}

.skills-info-content {
    padding: 2rem;
}

.skill-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.skill-info p {
    font-size: 1rem;
    font-weight: 500;
}

.skill-progress-bg {
    width: 100%;
    height: 0.5rem;
    background: #382e68;
    border-radius: 0.5rem;
    margin: 1rem 0;
    overflow: hidden;
}

.skill-progress {
    width: 100%;
    height: 0.5rem;
    background: linear-gradient(90deg, #aa94fe -50%, #7d60e6 50%);
    border-radius: 0.5rem;
    transition: all 0.5s ease-in-out;
}

.icons {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    margin-top: 2.5rem;
}
.icons>div {
    width: 4.2rem;
    height: 4.2rem;
    background-color: #2a2252;
    border-radius: 0.5rem;
    border: 1.5px solid #382e68;
}
.icons img {
    width: 4rem;
    padding: 0.75rem;
    filter: contrast(130%);
}

.active-skill {
    opacity: 1;
}
.unactive-skill {
    opacity: 0;
}

.icons>div>img:hover {
    transform: translateY(-0.5rem);
}

@media (max-width: 1025px)
{
    .skills-info-card h6 {
        font-size: 1.1rem;
    }
    .skill-info p {
        font-size: 0.9rem;
    }
    .skills-info-content {
        padding: 1.5rem;
    }
}